import React, {useEffect, useRef, useState} from 'react';
import Lottie from 'react-lottie';
import classNames from 'classnames';
import {gsap} from "gsap";
import {ScrollToPlugin} from "gsap/ScrollToPlugin";
import {useForm, Controller} from 'react-hook-form';
import {ErrorMessage} from '@hookform/error-message';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import InputMask from "react-input-mask";
import {faInstagram, faVk} from "@fortawesome/free-brands-svg-icons";

import style from './style.module.scss';

import headerBg from '../../assets/lotties/simpleHeaderBgY.json';
import arrowDown from '../../assets/lotties/blackTripleArrow.json';
import statsBg from '../../assets/lotties/statsBg.json';
import consultationBg from '../../assets/lotties/consultation.json';
import message from '../../assets/lotties/message.json';

import {SectionWrapper} from "../../components/Wrappers";

gsap.registerPlugin(ScrollToPlugin);

